select.job-detail-section-input {

    /* make arrow and background */
    background-image: -webkit-linear-gradient(45deg, transparent 50%, #49AE86 50%),
    -webkit-linear-gradient(315deg, #49AE86 50%, transparent 50%),
    -webkit-linear-gradient(left, #fff, #fff);
    background-image: linear-gradient(45deg, transparent 50%, #49AE86 50%),
    linear-gradient(135deg, #49AE86 50%, transparent 50%),
    linear-gradient(to right, #fff, #fff);
    background-position: calc(100% - 20px) calc(1em + 2px),
    calc(100% - 15px) calc(1em + 2px),
    calc(100% - 2.5em) 0.5em;
    background-size: 5px 5px,
    5px 5px,
    1px 1.5em;
    background-repeat: no-repeat;

    /* reset */
    border-radius: 5px;
    margin: 0;
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
}

select.job-detail-section-input:focus {

    /* make arrow and background */
    background-image: -webkit-linear-gradient(45deg, #49AE86 50%, transparent 50%),
    -webkit-linear-gradient(315deg, transparent 50%, #49AE86 50%),
    -webkit-linear-gradient(left, #fff, #fff);
    background-image: linear-gradient(45deg, #49AE86 50%, transparent 50%),
    linear-gradient(135deg, transparent 50%, #49AE86 50%),
    linear-gradient(to right, #fff, #fff);
    background-position: calc(100% - 15px) 1em,
    calc(100% - 20px) 1em,
    calc(100% - 2.5em) 0.5em;
    background-repeat: no-repeat;
}

